/* src/App.css â€” mobile header/tab/filter fixes only
   - Keep other styles intact; this file focuses on title/tab/filter responsive behavior.
   - DO NOT edit other files.
*/

:root{
  --bg:#f6fbfc;
  --card:#ffffff;
  --muted:#6b7b86;
  --accent-start:#2bd0c8;
  --accent-end:#1f6ef8;
  --pill:#e9f6ff;
  --focus:#2ea8ff;
}

/* base */
*{box-sizing:border-box;font-family: 'Inter', 'Helvetica Neue', Arial, sans-serif}
body,html,#root{height:100%;margin:0;background:var(--bg);color:#0b1a23}
.app{max-width:900px;margin:0 auto;padding:14px 12px 80px}

/* HEADER / TITLE / TABS
   - Ensure title never wraps; shrink on small screens
   - Tabs stay on single line; become horizontally scrollable if necessary
*/
.app-header{
  display:flex;
  align-items:center;
  justify-content:space-between;
  gap:12px;
  flex-wrap:nowrap;
  align-items:center;
  margin-bottom:6px;
}

.title{
  font-size:20px;
  margin:0;
  font-weight:700;
  white-space:nowrap;           /* prevent wrapping */
  overflow:hidden;
  text-overflow:ellipsis;
  max-width:62%;               /* allow space for tabs/controls on desktop */
}

/* Tabs: keep inline; allow horizontal scroll on tiny screens */
.tab-buttons{
  display:flex;
  gap:8px;
  align-items:center;
  flex:1 1 auto;
  justify-content:flex-end;
  min-width:0;                 /* allow shrink */
  overflow-x:auto;             /* if too many, scroll horizontally */
  -webkit-overflow-scrolling:touch;
}
.tab{
  padding:6px 10px;
  border-radius:12px;
  border:none;
  background:transparent;
  color:#0b1a23;
  cursor:pointer;
  white-space:nowrap;
  flex:0 0 auto;
  font-size:14px;
}
.tab.active{
  background:linear-gradient(90deg,var(--accent-start),var(--accent-end));
  color:#fff;
  box-shadow:0 6px 18px rgba(31,110,248,0.12);
}

/* TOP ROW: average + filter area */
.top-row{display:flex;align-items:flex-start;gap:10px;flex-wrap:wrap}
.avg-card{padding:10px;border-radius:10px;background:var(--card);box-shadow:0 8px 20px rgba(12,28,58,0.06);min-width:160px}
.avg-label{color:var(--muted);font-size:12px}
.avg-number{font-size:20px;font-weight:800}

/* FILTER area: ensure fits on mobile (shrink controls instead of wrapping) */
.filter-row{
  display:flex;
  align-items:center;
  gap:8px;
  flex: 0 1 auto;
  justify-content:flex-end;
  min-width:0;
}
.filter-left{display:flex;align-items:center;gap:8px;min-width:0}
.filter-label{color:var(--muted);font-size:13px;white-space:nowrap}
.filter-select{
  padding:6px 8px;border-radius:8px;border:1px solid #e6eef5;background:#fff;
  min-width:90px; max-width:40vw; width:auto;
  font-size:13px;
}
.filter-right{display:flex;align-items:center;gap:8px}
.reset-ui-btn{
  padding:6px 10px;border-radius:10px;border:none;background:#fff;color:#1b6ea8;cursor:pointer;box-shadow:0 6px 12px rgba(13,26,40,0.04);
  white-space:nowrap;
  font-size:13px;
  flex:0 0 auto;
}

/* Make sure filter-left can shrink before wrapping; if still doesn't fit, user can scroll tabs or controls */
.filter-row > * { flex-shrink: 1; }

/* Controls & general */
.controls{margin-top:10px;display:flex;flex-direction:column;gap:8px}
.control-head{display:flex;align-items:center;gap:8px}
.info-btn{border-radius:50%;width:26px;height:26px;background:#eef6fb;border:none;cursor:pointer;display:inline-flex;align-items:center;justify-content:center}
.legend{font-size:12px;color:var(--muted)}

/* Hand cards, results, etc. (unchanged; keep same layout) */
.hand-cards{display:flex;gap:10px;margin-top:12px;overflow-x:auto;padding:8px 6px 6px;background:transparent}
.hand-card{flex:0 0 110px;padding:14px 10px;border-radius:12px;background:linear-gradient(180deg,#fff,#fbfdff);text-align:center;box-shadow:0 10px 30px rgba(12,28,58,0.06);cursor:pointer;border:2px solid transparent;white-space:nowrap}
.hand-card.selected{border-color:var(--focus);transform:none}
.hand-emoji{font-size:28px}
.hand-text{margin-top:6px;font-weight:700}
.hand-sub{margin-top:6px;color:var(--muted);font-size:12px;display:flex;flex-direction:column;gap:4px}

/* result and actions compact */
.result-row{display:flex;gap:8px;margin-top:10px;flex-wrap:nowrap}
.pill{padding:8px 12px;border-radius:16px;border:none;background:var(--pill);cursor:pointer}
.pill.active{background:linear-gradient(90deg,var(--accent-start),var(--accent-end));color:white}

.actions-row{display:flex;gap:8px;align-items:center;margin-top:10px}
.save-btn{padding:10px 14px;border-radius:14px;border:none;background:linear-gradient(90deg,var(--accent-start),var(--accent-end));color:white;font-weight:700;cursor:pointer}
.back-btn.small{padding:8px 10px;border-radius:10px;background:#f3f7fa;border:none;cursor:pointer}

/* history table: keep nowrap so table cells not wrap unexpectedly */
.history-section{margin-top:16px}
.history-table{width:100%;border-collapse:collapse;background:var(--card);border-radius:8px;overflow:hidden;box-shadow:0 10px 30px rgba(12,28,58,0.06)}
.history-table th, .history-table td{padding:10px 12px;border-bottom:1px solid #f3f7fb;white-space:nowrap;font-size:13px}
.td-hand{text-align:center;font-size:16px}
.small-btn{padding:6px 10px;border-radius:8px;border:none;background:#eef6fb;color:#1b6ea8;cursor:pointer}

/* Stats/chart area unchanged (not part of this change) */

/* ---------------------
   MOBILE TUNING (these are the important fixes)
   - shrink title and tabs so they don't wrap
   - make filter/select smaller and allow min width to fit
   - ensure nothing wraps into 2 lines vertically
   --------------------- */
@media (max-width:520px){
  .app{padding:12px 10px 80px}
  .title{font-size:16px;max-width:56%}
  .tab{padding:6px 8px;font-size:13px}
  .tab-buttons{gap:6px}
  .filter-select{min-width:70px; max-width:36vw; font-size:13px}
  .reset-ui-btn{padding:6px 8px;font-size:12px}
  .avg-number{font-size:16px}
  .hand-card{flex:0 0 86px;padding:10px}
  .result-row{gap:6px}
}

/* very small screens: allow tabs to scroll horizontally (prevent wrapping to 2 rows) */
@media (max-width:380px){
  .tab-buttons{overflow-x:auto;-webkit-overflow-scrolling:touch}
  .title{font-size:15px}
  .filter-select{min-width:64px}
  .reset-ui-btn{padding:5px 6px}
}

/* utility */
.nowrap-col{white-space:nowrap;overflow:hidden;text-overflow:ellipsis}
